.modal-menu-wrap {
  height: 109px;
}
.modal-menu {
  &:not(.show) {
    position: relative;
    display: block !important;
    opacity: 1;
    z-index: 2;
  }
  .modal-dialog {
    max-width: none !important;
    margin: 0 !important;
    padding: 0 !important;
    transform: none !important;
  }
  .modal-content {
    padding: 0 !important;
    border: none !important;
  }


  &.show {
    .modal-dialog {
      margin-right: -17px !important;
      @media (min-width: 576px) {
        margin-left: -17px !important;
      }
    }
    .collapse-menu {
      display: block;
    }
    .header {
      padding: 37px 0 0;
      background: white;
      .header__logo {
        background-image: url("/build/img/svg/logo-footer.svg");
      }
      .header__call-back {
        display: block;
        visibility: visible;
      }
      .link-lk {
        color: #6F727B;
      }
      .header__menu-open {
        fill: $color-red;
      }
    }
  }
}