.bread-crumbs {
  padding-top: 20px;
}
.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin-bottom: 1rem;
  list-style: none;
  background-color: transparent;
  border-radius: 0;
  font-weight: 300;
  font-size: 14px;
}

.breadcrumb-item.active {
  color: $color-red;
}

